import { styleTags, tags as t } from "@lezer/highlight";

export const highlighting = styleTags({
    // 
  KeywordControl: t.controlKeyword,              // 
  SupportFunction: t.function(t.keyword),        // 
  KeywordOther: t.keyword,                        // 
  EntityNameType: t.typeName,                    // 
  ReasoningPhrase: t.meta,                        // 
  Comment: t.lineComment,                         // 
  OpenParen: t.paren,                             // 
  CloseParen: t.paren,                            // 
  OpenBrace: t.brace,                             // 
  CloseBrace: t.brace,                            // 
  OpenBracket: t.squareBracket,                   // 
  CloseBracket: t.squareBracket,                  // 
});